/* ----------------ALL APP---------------- */

html{
  box-sizing: border-box;
}
*, *:before, *:after{
  box-sizing: inherit;
}
.content-wrap {
    max-width: 900px;
    margin: 0 auto;
    padding: 30px;
    overflow: auto;
}
.App {
  text-align: center;
  background-image: linear-gradient(to bottom right, #663F46, #41292d);
  /* height: 100vh; */
}


/* ----------------MAIN CONTENT BTWN HEADER AND FOOTER---------------- */

.main {
    min-height: calc(100vh - 215.88px);
}

/* ----------------HEADER FOOTER---------------- */

.App-header {
  color: #534D41;
  background-color: #F3A712;
  font-family: "Josefin Sans";
}

footer {
  background-color: #B8C4BB;
}
footer a{
  color: #534D41;
  padding: 10px;
  text-decoration: none;
}

footer a:hover{
  color: #663F46;
  text-decoration: underline;
}



/* -------------RUNNING WRAPPERS--------------- */

.startOverGoBackBtns {
  width: 250px;
  margin: auto;
  display: inline;
  /* background-color.: #456; */
}

.runningWrapper {
  color: #222;
  text-align: center;
  /* height: 68vh; */
}

.btnsWrapper {
  /* background-color: #d4d; */
  margin: 40px;
  display: inline-block;
  width: 50vw;
}

.btnAWrapper{
  /* background-color: #2af; */
  margin: 10px;
}

.btnBWrapper {
  /* background-color: #0e0; */
  margin: 10px;
}

/* ---------------STRING OF CLICKED-------------- */

.prevClickedString {
  font-size: 1.25em;
  width: 80%;
  /* font-weight: bold; */
  color: #E8F7EE;
  /* background-color: #333; */
  margin: auto;
}


/* -----------RESULT-------------- */

.resultSection h1 {
  font-size: 2.5em;
  font-family: "Josefin Sans";
  color: #E8F7EE;
}

.resultSection h3 {
  font-size: 2em;
  font-family: "Josefin Sans";
  color: #E8F7EE;
}

