/* ----------A & B---------- */

.btns {
  margin: 15px;
  padding: 10px;
  /* text-align: center; */
  /* width: auto; */
  /* height: auto; */
  /* display: inline-block; */
  /* position:relative; */
  font-family: "Libre Franklin";
  color: #534D41;
  font-size: 1.25em;
  background-color: #F3A712;
  border: 2px solid #DD9811;
  border-radius: 8px;
  box-shadow: 1px 2px #222;
  cursor: pointer;
}


/* ---------START--------- */

.startButton {
    color: #fff;
    MARGIN: 30vh 0px 30vh 0px;
    background-color: #11dd99;
    border: 2px solid #11dd99;
    border-radius: 10px;
    box-shadow: 1px 2px #222;
    cursor: pointer;
    font-size: 1.25em;

}


/* ---------START OVER / GO BACK----------- */

.startOverButton {
    color: #534D41;
    background-color: #E8F7EE;
    border: 2px solid #B8C4BB;
    border-radius: 10px;
    box-shadow: 1px 2px #222;
    cursor: pointer;
    font-size: 1.25em;
    margin: 15px;

}

.goBackButton {
  color: #534D41;
  background-color: #E8F7EE;
  border: 2px solid #B8C4BB;
  border-radius: 10px;
  box-shadow: 1px 2px #777;
  cursor: pointer;
  font-size: 1.25em;

}

